import React, {useState} from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import { Form, Button, Container, Row, Col, FormControl } from 'react-bootstrap';

const AuthIdComponent = () =>{
    const [memberId, setMemberId] = useState("");
    const navigate = useNavigate();

    const GetInputId = (e) => {
        const memberId = e.target.value;
        setMemberId(memberId);
    }

    const AuthId = (e) => {
        e.preventDefault();
        const data = new FormData();
        data.append("memberId", memberId);
        const checkUrl = `/api/opentalk/auth/checkId`
        axios.post(checkUrl, data)
        .then((res) => {
            if (res.data){
                navigate("/opentalk/member/findPw")
            }
            else{
                alert("  .")
            }
        })
        .catch((error) => console.log(error));
    }

    return (
        <Container style={{ minHeight: '100vh'}}>
            <Row>
                <Col xs lg="3" md={{ span: 3, offset: 4 }} className="border border-warning border-3 rounded-3 p-5">
                <h3> </h3>
                <Form>
                    <Form.Label></Form.Label>
                    <FormControl type='text' value={memberId} onChange={GetInputId}></FormControl>
                    <br></br>
                    <div className="d-grid gap-2">
                        <Button onClick={AuthId}></Button>
                    </div>
                </Form>
                </Col>
            </Row>
        </Container>
    );
}

export default AuthIdComponent;