import React, {useState, useEffect} from 'react';
import { useNavigate } from 'react-router-dom';
import Modal from 'react-modal';
import axios from'axios';
import { Form, Button, Container, Row, Col, InputGroup, 
    InputGroupText, FormControl, FormGroup, FormLabel,
    ListGroup,
    ListGroupItem, } from 'react-bootstrap';

export const SetRoomComponent = ({onDataUpdate}) =>{
    const [isOpen, setIsOpen] = useState(false);
    const [roomName, setRoomName] = useState("");
    const [roomId, setRoomId] = useState("");
    const [participants, setParticipants] = useState(3);
    const [existLock, setExistLock] = useState(false);
    const [password, setPassword] = useState("");
    const [manager, setManger] = useState();
    const [info, setInfo] = useState("");
    const [tag, setTag] = useState("");
    const [tags, setTags] = useState([]);

    const navigate = useNavigate();

    useEffect(() => {
        const fetchManager = async () =>{
            try{
                const response = await axios.get("/api/opentalk/member/me", {
                    headers: {Authorization: localStorage.getItem('refresh-token')}
                })
                setManger(response.data);
            } catch (error) {
                console.log(error);
            }
        }
        fetchManager();
        console.log(manager);
    }, []);
    
    const openModal = () => {
        setIsOpen(true)
    };
    const closeModal = () => {
        setRoomName('');
        setExistLock(false);
        setPassword('');
        setParticipants(0);
        setIsOpen(false);
        setTags([]);
        setInfo('');
    }

    const GetInputName = (event) => {
        setRoomName(event.target.value);
    }
    const GetInputParticipates = (event) => {
        if (event.target.value >= 3){
            if (event.target.value > 20){
                window.alert("   20 .");
            }
            else{
                setParticipants(event.target.value);
            }
        }
        else{
            window.alert("   3 .");
        }
    }
    const GetInputPassword = (event) => {
        setPassword(event.target.value);
    }
    const GetCheckExistPw = (event) => {
        setExistLock(event.target.checked);
        if (!event.target.checked){
            setPassword('');
        }
    }
    const GetInputInfo = (event) => {
        setInfo(event.target.value);
    }
    const GetInputTag = (event) => {
        setTag(event.target.value);
    }

    const AppendTag = (getTag) => {
        let isExist = false;
        for (let i = 0; i < tags.length; i++){
            if (tags[i].tagName === getTag){
                isExist = true;
            }
        }
        if (tags.length >= 5){
            window.alert("   5  .");
        }
        else{
            if (getTag === ""){
                window.alert(" .");
            }
            else if (isExist){
                window.alert("  .");
            }
            else{
                const newTags = [...tags, {
                    tagName: getTag,
                    accumulate: 0
                }];
                setTags(newTags);
                setTag("");
            }
        }
    }

    const tagDelete = (getTag) => {
        const newTags = tags.filter(tag => tag !== getTag);
        setTags(newTags);
    }


    const MakeRoom = () => {
        const makeUrl = `/api/opentalk/makeRoom`
        console.log(manager);
        axios.post(makeUrl, {
            "roomName": roomName,
            "roomPassword": password,
            "roomManager": manager.memberNickName,
            "limitParticipates": participants,
            "introduction": info,
            "existLock": existLock,
            "members": [],
            "roomTags": tags
        })
        .then((res)=>{
            if (res.status === 200){
                alert(" .");
                setRoomId(res.data);
                closeModal();
                onDataUpdate(prevState => !prevState);
            }
        })
        .catch((error) => console.log(error));
        
    };

    return (
        <Container>
            <div className="d-grid gap-2">
                <Button onClick={openModal}> </Button>
            </div>
            <Modal
                isOpen={isOpen} 
                onRequestClose={closeModal}
                style={{
                    content: {
                        width: '800px', //   
                        height: '400px', //   
                    }
                }}>
                <Row>
                    <Col>
                        <InputGroup>
                            <InputGroup.Text> </InputGroup.Text>
                            <FormControl type='text' value={roomName} onChange={GetInputName}></FormControl>
                        </InputGroup>
                        <br></br>
                        <InputGroup>
                            <InputGroup.Text></InputGroup.Text>
                            <FormControl type="number" min={3} max={20} value={participants} onChange={GetInputParticipates}></FormControl>
                        </InputGroup>
                        <br></br>
                        <InputGroup>
                            <InputGroup.Text></InputGroup.Text>
                            <Form.Check type='checkbox' checked={existLock} onChange={GetCheckExistPw}/>
                            <FormControl type='password' value={password} onChange={GetInputPassword} disabled={!existLock}></FormControl>
                        </InputGroup>
                        <br></br>
                        <InputGroup>
                            <FormControl type="text" value={info} placeholder=' ' onChange={GetInputInfo}></FormControl>
                        </InputGroup>
                        <br></br>
                        <InputGroup>
                            <FormControl type='text' value={tag} onChange={GetInputTag}></FormControl>
                            <Button onClick={()=>AppendTag(tag)}> </Button>
                        </InputGroup>

                        <ListGroup className="list-group list-group-horizontal">
                            {tags.map((t)=> (
                                <ListGroupItem>#{t.tagName}<Button onClick={()=>tagDelete(t)}></Button></ListGroupItem>
                            ))}
                        </ListGroup>
                        <br></br>
                        <Button onClick={MakeRoom}> </Button>
                        <Button onClick={closeModal}> </Button>
                    </Col>
                </Row>
            </Modal>
        </Container>
    );
}; 

export default SetRoomComponent;