import React, {useState} from 'react';
import {useNavigate} from 'react-router-dom';
import axios from 'axios'
import { Form, Button, Container, Row, Col, FormGroup } from 'react-bootstrap';
import MainComponent from './main';

const LoginComponent = (props) => {
    
    const [memberId, setMemberId] = useState("");
    const [memberPw, setMemberPw] = useState("");
    const [isForget, setIsForget] = useState(false);
    const navigate = useNavigate();
    
    const CheckLogin = (e) => {
        const checkloginUrl = '/api/opentalk/auth/login'

        if (memberId === ""){
            alert(" .")
        }
        else if (memberPw === ""){
            alert(" .")
        }
        else{
            axios.post(checkloginUrl, {
                memberId: memberId,
                memberPassword: memberPw,
            })
            .then((res) => {
                if (res.status === 200){
                    localStorage.setItem('refresh-token', res.headers['authorization']);
                    navigate("/opentalk/main");
                }
            })
            .catch((error)=>{
                if (error.response && error.response.status === 401) {
                    alert("   .");
                } else {
                    console.error('Error:', error);
                }
            });
        }
        
    }

   return (
    <Container style={{ minHeight: '100vh'}}>
            <Row>
                <Col md={{ span: 3, offset: 4 }} className="border border-warning border-3 rounded-3 p-5">
                    <h2></h2>
                    <Form onSubmit={CheckLogin}>
                        <FormGroup>
                            <Form.Label></Form.Label>
                            <Form.Control type="text" placeholder=" " value={memberId} onChange={(e) => setMemberId(e.target.value)} />
                        </FormGroup>
                        <FormGroup>
                            <Form.Label></Form.Label>
                            <Form.Control type="password" placeholder=" " value={memberPw} onChange={(e) => setMemberPw(e.target.value)} />
                        </FormGroup>
                        <br></br>
                        <div className="d-grid gap-2">
                            <Button variant="primary" onClick={CheckLogin} size='4'></Button>
                            <Button variant="secondary" onClick={() => navigate("/opentalk/member/enroll")}></Button>
                            <Button variant='warning' onClick={() => setIsForget(prevState => !prevState)}> ?</Button>
                            {isForget && (
                            <div className="d-grid gap-2">
                                <Button variant="primary" onClick={() => navigate("/opentalk/member/findId")}> </Button>
                                <Button variant="primary" onClick={() => navigate("/opentalk/member/authId")}> </Button>
                            </div>
                            )}
                            <Button variant="info" onClick={() => navigate("/")}></Button>
                        </div>
                    </Form>
                </Col>
            </Row>
        </Container>

    );
}

export default LoginComponent;